<template>
<div class="index">
  <p></p>
  <ul>
  <li v-for="post in posts" :key="post.ID">
     : <a>{{ post.Title }}</a>
  <li>
  </ul>
<button v-on:click="getPost">Greet</button>
</div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      posts: null
    }
  },
  created :function() {
    this.axios.get('/posts')
    .then(response => this.posts = response.data)
  }
}
</script>


